.kb-toggle-icon-style-xclose.kb-toc-toggle-active .kb-table-of-contents-icon-trigger:before, .kb-toggle-icon-style-xclosecircle.kb-toc-toggle-active .kb-table-of-contents-icon-trigger:before {
